.single-post{


    $this: &;

    &__container
    {
       padding-top: 100px;
    }


    &__return{
        margin-top: 10px;

        @include align-width--l;
        a{
            color: #5C5C5C;
            font-family: var(--type--1--font-family);
            text-decoration: none;
            font-weight: 700;

        }

        a:before{
            content: "";
            display: inline-flex;
            height: 20px;
            width: 20px;
            background-repeat: no-repeat;
            background-image: url('/wp-content/themes/reach/_src/images/arrow-right.svg');
            transform: rotate(180deg);
            top: 0px;
            right: 10px;
            position: relative; 
            margin-bottom: 40px;
        }
       
    }

    &__content{
       max-width: 1200px;
       margin-left: auto;
       margin-right: auto;
    }


    
    &__category{
        border-radius: 99px;
        background-color: var(--color--blue);
        width: fit-content;
        padding: 2px 15px;
        color: #5C5C5C;

       ul{
            list-style: none;
            text-decoration: none;
            margin-top: 0px;
            margin-bottom: 0px;
            padding: 0px;
            color:#5C5C5C;
       }

        a{
            text-decoration: none;
            color: #5C5C5C;
            font-family: var(--type--1--font-family);
            font-weight: 600;
       }

    }

    &__date{

        color: var(--color--lightgrey);
        position: relative;

        margin-top: 0px;
        margin-bottom: 2em;

        &::before{
            background-image: url('/wp-content/themes/reach/_src/images/calendar.svg');
            content: " ";
            display: inline-flex;
            background-repeat: no-repeat;
            width: 30px;
            height: 30px;
            position: relative;
            top:10px;
        }
    }


    &__title{
        font-size: 2.5em;
        font-weight: 700;
        margin-top: 10px;
        margin-bottom: 0px;
        color: #5C5C5C;


        h1{
            font-family: var(--type--1--font-family);
            margin-bottom: 10px;
        }
       
    }

    &__content{
        padding-bottom:30px;
    }


    &__image{

        img{
            width: 100%;
            height: auto;
            border-radius: 30px;
        }

        margin-bottom: 5em;
    }























}